.video__player {
  position: relative;
  height: 200px;

  @media (min-width: $tablet-width) {
    height: 500px;
  }
}

.video__frame {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video__controls {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 23px;
  background: $controls-background;

  @media (max-width: $mobile-width-only) {
    margin: 0 20px;
  }

  @media (min-width: $tablet-width) {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 46px;
    padding: 0 30px;
    align-items: center;
    background: $volume-bar-background;
  }
}

.video__volume-bar {
  position: absolute;
  top: -46px;
  left: 0;
  right: 0;
  padding: 23px 0;
  background: $volume-bar-background;

  @media (min-width: $tablet-width) {
    position: relative;
    width: 100%;
    top: 0;
    left: 10px;
    right: 10px;
    background-color: transparent;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25px;
    right: 25px;
    z-index: 1;
    height: 3px;
    margin: auto;
    background-color: rgba($body-background, 0.3);
    cursor: pointer;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25%;
    z-index: 2;
    width: 9px;
    height: 9px;
    margin: auto;
    background-color: #ffffff;
    border-radius: 50%;
    cursor: pointer;
  }
}

.video__button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: 1px solid;

  &--subtitles {
    margin: 0 30px;
  }
}

.video__button-group {
  display: flex;
}
