*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  font-family: inherit;
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea {
  font-family: inherit;
}

h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  padding: 0;
  border: none;
}
